<template>
  <div class="container">
      <div class="search-container">
            <input class="search-input" v-model.trim="value" type="text" placeholder="search" @keyup.enter="search">
            <svg class="search-icon" width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path opacity="0.8" fill-rule="evenodd" clip-rule="evenodd" d="M16.9816 9.61047C16.9816 13.1526 13.9625 16.2209 9.9908 16.2209C6.01908 16.2209 3 13.1526 3 9.61047C3 6.06828 6.01908 3 9.9908 3C13.9625 3 16.9816 6.06828 16.9816 9.61047ZM15.3467 17.7247C13.7988 18.6721 11.9614 19.2209 9.9908 19.2209C4.47303 19.2209 0 14.9182 0 9.61047C0 4.30275 4.47303 0 9.9908 0C15.5086 0 19.9816 4.30275 19.9816 9.61047C19.9816 11.9596 19.1054 14.1118 17.6503 15.7813L24.0019 21.9159L21.8441 24L15.3467 17.7247Z" fill="black"/>
            </svg>

            <div class="label-container">
                <Label v-for="item in labelArray" :key="item" @click.native="setSearchValue(item)" :label="item" :inputValue="value"></Label>
            </div>

            <div style="margin-bottom: 25px;" v-if="searchRes.length > 0">
                <ResItem v-for="res in searchRes" :key="res.title" :res="res"></ResItem>
            </div>

            <div class="place-image" v-else-if="searchRes.length === 0">
                占位图
            </div>

            <div class="bottom-result">{{ searchRes.length > 0 ? searchRes.length : 'No' }} result</div>

      </div>
  </div>
</template>

<script>
import Axios from "axios";
import Label from "../components/Label.vue";
import ResItem from "../components/ResItem.vue";
export default {
    name: 'SearchBar',
    components: {
        Label,
        ResItem,
    },
    data() {
        return {
            value: '',
            labelArray: ['Languages', 'Build', 'Design', 'Cloud'],
            searchRes: [],
        }
    },
    methods: {
        search(e) {
            console.log('search', e.target.value);
            let value = e.target.value;
            let url = `https://frontend-test-api.digitalcreative.cn/?no-throttling=true&search=${value}`
            Axios.get(url).then(res => {
                console.log(res.data);
                if (res.status === 200) {
                    this.searchRes = res.data;
                }
                
            })
        },
        setSearchValue(data) {
            console.log(data);
            this.value = data;
        }
    }
}
</script>

